.main-p{
    display: block;
    height: 100%;
    width: 100%;
    padding: 20px;
}

.logo-p{
   

    width: 84px;
    position: absolute; 
    left: 50%;
    margin-left: -42px;
    top:20%;

}

.text-div-p{
    text-align: center;
    display: flex;
    
    flex-direction: column;
    padding: 10px;
    gap: 12px;

    position: absolute;
    left: 20%;
    right: 20%;
    top: 30%;
}

.header-p{
    max-lines: 1;
    

    font-family: 'SF Pro Display';
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    /* identical to box height */

    text-align: center;

    color: #000000;


    /* Inside auto layout */

    flex: none;
    order: 0;
    flex-grow: 0;
}

.discription-p{
/* есо поможет тебе в этом */

    font-family: 'SF Pro Display';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    /* identical to box height */

    text-align: center;
    letter-spacing: 0.0025em;

    color: #000000;


    /* Inside auto layout */

    flex: none;
    order: 1;
    flex-grow: 0;
}